  top: 100%;
  left: 0.05em;
  right: 0.05em;
  background: currentColor;
  transform-origin: 100% 50%;
  transform: scaleX(0);
  transition: -webkit-transform 0.6s cubic-bezier(1, 0, 0, 1);
  transition: transform 0.6s cubic-bezier(1, 0, 0, 1);
