.splitting.cells img {
  width: 100%;
  display: block;
}

@supports (display: grid) {
  .splitting.cells {
    position: relative;
    overflow: hidden;
    background-size: cover;
    visibility: hidden;
  }

  .splitting .cell-grid {
    background: inherit;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template: repeat(var(--row-total), 1fr)/repeat(var(--col-total), 1fr);
  }

  .splitting .cell {
    background: inherit;
    position: relative;
    overflow: hidden;
  }

  .splitting .cell-inner {
    background: inherit;
    position: absolute;
    visibility: visible;
    width: calc(100% * var(--col-total));
    height: calc(100% * var(--row-total));
    /* Position properly */
    left: calc(-100% * var(--col-index));
    top: calc(-100% * var(--row-index));
  }

  .splitting .cell {
    --center-x: calc((var(--col-total) - 1) / 2);
    --center-y: calc((var(--row-total) - 1) / 2);
    --offset-x: calc(var(--col-index) - var(--center-x));
    --offset-y: calc(var(--row-index) - var(--center-y));
    --distance-x: calc( (var(--offset-x) * var(--offset-x)) / var(--center-x) );
    --distance-y: calc( (var(--offset-y) * var(--offset-y)) / var(--center-y) );
  }
}
.splitting .cell,
.splitting .cell__inner,
.splitting img {
  transition: transform 1s cubic-bezier(0.63, 0.01, 0.07, 0.99), opacity 1s linear;
  /* No animation on leave */
  transition-delay: calc( 0.1s * var(--cell-index) );
  /* Reverse the animation */
  transition-delay: calc( 0.1s * (var(--cell-total) - var(--cell-index)) );
}
.splitting img {
  transform: scale(0.8);
  transition-delay: 0s;
  visibility: visible;
  opacity: 0;
}
.splitting:hover .cell,
.splitting:hover .cell__inner {
  transition-delay: calc( 0.1s * var(--cell-index) );
}
.splitting:hover img {
  opacity: 0.99;
  transform: scale(1);
  transition-delay: calc( 0.1s * var(--cell-total) );
}

.splitting-img.img-vertical:hover .cell {
  transform: translateY(100%);
}

.splitting-img.img-horizontal:hover .cell {
  transform: translateX(100%);
}

